import WildEmitter from 'wildemitter';
//import '../lib/getStats';
import getStats, { GetStatsResult } from '../lib/getStats';

export interface MediaStats {
    speed: number;
    packetsLost: number;
    packetsTransferred: number;
    packetsLostRate: number;
    bytesTransferred: number;
    /*resolutionW: number;
    resolutionH: number;
    videoCodecs: string;
    audioCodecs: string;
    ipAddress: string;*/
}

export class GetStats extends WildEmitter {
    private _pc: RTCPeerConnection | null = null;
    private _rawResult: GetStatsResult | null = null;
    private _sendStats: MediaStats = {
        speed: 0,
        packetsLost: 0,
        packetsTransferred: 0,
        packetsLostRate: 0,
        bytesTransferred: 0
        /*resolutionW: 0,
        resolutionH: 0,
        videoCodecs: '',
        audioCodecs: '',
        ipAddress: '',*/
    };
    private _recvStats: MediaStats = {
        speed: 0,
        packetsLost: 0,
        packetsTransferred: 0,
        packetsLostRate: 0,
        bytesTransferred: 0
        /*resolutionW: 0,
        resolutionH: 0,
        videoCodecs: '',
        audioCodecs: '',
        ipAddress: '',*/
    };
    private _isDestroyed = false;

    constructor(pc?: RTCPeerConnection) {
        super();
        if (pc) {
            this.setPC(pc);
        }
    }

    public setPC(pc: RTCPeerConnection) {
        if (this._pc !== pc) {
            getStats(pc, (res) => this._invokeRawResult(res), 1000);
            this._pc = pc;
        }
        this._isDestroyed = false;
    }

    public getStats(): { send: MediaStats; recv: MediaStats } {
        return {
            send: this._sendStats,
            recv: this._recvStats
        };
    }

    public destroy() {
        this._isDestroyed = true;
        if (this._rawResult) {
            this._rawResult.nomore();
        }
    }

    private _invokeRawResult(r: GetStatsResult) {
        if (this._rawResult && this._rawResult !== r) {
            this._rawResult.nomore();
        }
        if (this._isDestroyed) {
            r.nomore();
            return;
        }
        this._rawResult = r;

        // send
        const ss = this._sendStats;
        ss.speed =
            Math.round(
                (parseFloat(String(r.video.send.availableBandwidth)) + parseFloat(String(r.audio.send.availableBandwidth))) * 10
            ) / 10;
        ss.packetsLost = parseInt(String(r.video.packetsLost)) || 0;
        ss.packetsTransferred = 0; //parseInt(String(r.video.bytesSent + r.video.bytesReceived)) || 0;
        ss.packetsLostRate = 0; //parseInt(String(r.video.packetsLost /(r.video.bytesSent + r.video.bytesReceived) )) || 0;
        ss.bytesTransferred = parseInt(String(r.audio.bytesSent)) + parseInt(String(r.video.bytesSent));
        /*ss.resolutionW = parseInt(String(r.resolutions.send.width));
        ss.resolutionH = parseInt(String(r.resolutions.send.height));
        ss.videoCodecs = r.video.send.codecs[0] || '';
        ss.audioCodecs = r.audio.send.codecs[0] || '';
        ss.ipAddress = r.connectionType.local.ipAddress[0] || '';*/

        // receive
        const rs = this._recvStats;
        rs.speed =
            Math.round(
                (parseFloat(String(r.video.recv.availableBandwidth)) + parseFloat(String(r.audio.recv.availableBandwidth))) * 10
            ) / 10;
        rs.packetsLost = parseInt(String(r.video.packetsLost)) || 0;
        rs.packetsTransferred = 0; //parseInt(String(r.video.bytesSent + r.video.bytesReceived)) || 0;
        rs.packetsLostRate = 0; //parseInt(String(r.video.packetsLost /(r.video.bytesSent + r.video.bytesReceived))) || 0;
        rs.bytesTransferred = parseInt(String(r.audio.bytesReceived)) + parseInt(String(r.video.bytesReceived));
        /*rs.resolutionW = parseInt(String(r.resolutions.recv.width));
        rs.resolutionH = parseInt(String(r.resolutions.recv.height));
        rs.videoCodecs = r.video.recv.codecs[0] || '';
        rs.audioCodecs = r.audio.recv.codecs[0] || '';
        rs.ipAddress = r.connectionType.local.ipAddress[0] || '';*/

        // emit event
        this.emit('stats', { send: ss, recv: rs });
    }
}
