import WildEmitter from 'wildemitter';
import * as stream from 'stream';

// 
export const MEDIA_STOP = 'stop';
export const MEDIA_STARTING = 'starting';
export const MEDIA_RECORDING = 'recording';
export const MEDIA_FAILED = 'failed';

type MediaStreamResolveFunc = (s: MediaStream) => void;
type MediaStreamRejectFunc = (err?: any) => void;

export interface ScreenMediaConfig {
    stream?: MediaStream;
    audio?: boolean;
    video?: boolean;
}

export class ScreenMedia extends WildEmitter {
    private _status: string = MEDIA_STOP; // 
    private _stream: MediaStream | null = null; // 

    // 
    private _resolveStreamQueue: Array<MediaStreamResolveFunc> = [];
    private _rejectStreamQueue: Array<MediaStreamRejectFunc> = [];

    constructor() {
        super();
    }

    public start(mediaConfig?: ScreenMediaConfig): Promise<MediaStream> {
        if (this._stream) {
            this._changeStatus(MEDIA_RECORDING);
            return Promise.resolve(this._stream);
        }

        this._changeStatus(MEDIA_STARTING);
        return this._doGetUserMedia(mediaConfig).then((stream: MediaStream) => {
            console.log('----------> return new stream',  stream);
            this._changeStatus(MEDIA_RECORDING);
            return stream;
        });
    }

    public stop() {
        if (this._stream) {
            this._changeStatus(MEDIA_STOP);
            this.emit('remove-stream', this._stream);
            this._stream.getTracks().forEach((track) => {
                track.stop();
            });
            this._stream = null;
        }
    }

    public getStatus(): string {
        return this._status;
    }

    private _changeStatus(status: string) {
        this._status = status;
        this.emit('status:' + status, status);
    }

    private _doGetUserMedia(constraints?: ScreenMediaConfig): Promise<MediaStream> {
        return new Promise((resolve, reject) => {
            navigator.mediaDevices
                // @ts-ignore
                .getDisplayMedia(constraints)
                .then((stream: MediaStream) => {
                    debugger
                    this._stream = stream;
                    console.log('------------> stream before emit', stream);
                    this.emit('has-stream', this._stream);

                    this._resolveStreamQueue.forEach((f) => f(stream));
                    this._resolveStreamQueue = [];
                    this._rejectStreamQueue = [];
                    return stream;
                })
                .then((stream: MediaStream) => {
                    // 
                    stream.getVideoTracks()[0].addEventListener('ended', () => {
                        if (this._status === MEDIA_RECORDING) {
                            this.emit('remove-stream', this._stream);
                            this._changeStatus(MEDIA_STOP);
                            this._stream = null;
                            this._resolveStreamQueue = [];
                            this._rejectStreamQueue = [];
                        }
                    });
                    resolve(stream);
                })
                .catch((e: Error) => {
                    this._rejectStreamQueue.forEach((f) => f(e));
                    this._resolveStreamQueue = [];
                    this._rejectStreamQueue = [];
                    reject(e);
                });
        });
    }

    public requestStream(): Promise<MediaStream> {
        debugger
        if (this._stream) {
            console.log('stream in requestStream()', stream);
            return Promise.resolve(this._stream);
        }
        return new Promise((resolve, reject) => {
            this._rejectStreamQueue.push(resolve);
            this._rejectStreamQueue.push(reject);
        });
    }
}
