import WildEmitter from 'wildemitter';
import { CommonData } from './common-data';
import { JanusManager } from './janus-manager';
import { Room, RoomStatus } from './room';
import { UserControl } from './user-control';
import { LocalMedia, MEDIA_RECORDING } from './local-media';
import { MediaStats } from './get-stats';
import {
    YoumeAvInputStatNotifyEvent,
    YoumeCommonStatusEvent,
    YoumeJoinLeaveUser,
    YoumeMemberChangeUser,
    YoumeMessageEvent,
    YoumeOnReceiveMessage
} from '../types/youmeEvent';
import { ScreenMedia } from './screen-media';
import { PublishType, StreamPublisher } from './stream-publisher';
import { StreamType } from './user';

export enum YoumeCommonStatusEventType {
    MIC_STATUS = 0, // 
    SPEAKER_STATUS = 1, // 
    MIC_CTR_STATUS = 2, // 
    SPEAKER_CTR_STATUS = 3, // 
    AVOID_STATUS = 4, // 
    IDENTITY_MODIFY = 5, //  <==> status  YouMeUserRole
    SWITCH_STREAM = 6 // 1 
}

interface IRoomPublishersMap {
    [roomName: string]: { [streamName: string]: StreamPublisher };
}

interface IRoomJoinUsersMap {
    [roomName: string]: { [userId: string]: string };
}

export class RoomControl extends WildEmitter {
    // 
    private _rooms: { [roomName: string]: Room } = {};

    // 
    private _publishers: IRoomPublishersMap = {};

    // 
    private _roomUsers: IRoomJoinUsersMap = {};

    constructor(
        private _commonData: CommonData,
        private _localMedia: LocalMedia,
        private _screenMedia: ScreenMedia,
        private _userControl: UserControl,
        private _janusManager: JanusManager
    ) {
        super();
        this._attachLocalMediaEvents();
        this._attachScreenMediaEvents();
    }

    public joinRoom(roomName: string): Promise<void> {
        if (this._rooms[roomName]) {
            this.leaveRoom();
        }
        this._publishers = { [roomName]: {} };
        const newRoom = new Room(roomName, this._commonData, this._localMedia, this._userControl, this._janusManager);
        this._attachRoomEvents(newRoom);
        const isLocalMediaRecording = this._localMedia.getStatus() === MEDIA_RECORDING;
        const isScreenMediaRecording = this._screenMedia.getStatus() === MEDIA_RECORDING;

        if (isLocalMediaRecording) {
            newRoom.publish();
        }

        this.donWhenRoomJoined(newRoom)
            .then(() => {
                if (isScreenMediaRecording) {
                    return this._screenMedia.requestStream();
                }
                return Promise.resolve(null);
            })
            .then((stream) => {
                if (stream) {
                    const streamName = 'share';
                    const pub = new StreamPublisher(roomName, streamName, stream, this._commonData, this._janusManager);
                    this._publishers[roomName][streamName] = pub;
                    pub.setShareType(PublishType.Share);
                    pub.publish();
                }
            });

        this._rooms[roomName] = newRoom;
        return new Promise((resolve, reject) => {
            if (newRoom.getStatus() === RoomStatus.IN) {
                resolve();
            } else {
                newRoom.once('status:in', () => resolve());
                newRoom.once('join-error', (err: Error) => reject(err));
            }
        });
    }

    public leaveRoom(roomName?: string) {
        if (roomName) {
            if (this._rooms[roomName]) {
                this._rooms[roomName].leave();
                delete this._rooms[roomName];
            }

            if (this._publishers[roomName]) {
                const roomPublishers = this.getRoomPublisherList(roomName);
                roomPublishers.forEach((pub) => {
                    pub.leave();
                });
                delete this._publishers[roomName];
            }
        } else {
            this.leaveAllRoom();
        }
    }

    // 
    public publishCustomStream(stream: MediaStream, streamName: string, roomName?: string): Promise<void> {
        return new Promise((resolve, reject) => {
            // 
            if (roomName) {
                const room = this._rooms[roomName];
                if (!room) {
                    return reject('room is not exist');
                }
                const pubList = this.getRoomPublisherList(roomName);
                const isExist = pubList.some((pub) => pub.getStreamName() === streamName);
                if (isExist) {
                    return reject(`streamName is exist in this room`);
                }

                return this.donWhenRoomJoined(room).then(() => {
                    const pub = new StreamPublisher(roomName, streamName, stream, this._commonData, this._janusManager);
                    pub.setShareType(PublishType.Coustom);
                    this._publishers[roomName][streamName] = pub;
                    pub.publish();
                });
            }

            // 
            const roomNames = this.getRoomNameList();
            const all = roomNames.map((roomName) => {
                const pubList = this.getRoomPublisherList(roomName);
                const isExist = pubList.some((pub) => pub.getStreamName() === streamName);
                if (isExist) {
                    // 
                    return Promise.resolve();
                }
                const room = this._rooms[roomName];
                return this.donWhenRoomJoined(room).then(() => {
                    const pub = new StreamPublisher(roomName, streamName, stream, this._commonData, this._janusManager);
                    pub.setShareType(PublishType.Coustom);
                    this._publishers[roomName][streamName] = pub;
                    pub.publish();
                });
            });
            return Promise.all(all);
        });
    }

    /**
     * 
     * @param streamName - 
     * @param roomName - 
     */
    public unpublishCustomStream(streamName: string, roomName?: string) {
        if (roomName) {
            const room = this._rooms[roomName];
            if (!room) {
                return;
            }
            const pub = this.getRoomPublisherList(roomName).find((pub) => pub.getStreamName() === streamName);
            if (!pub) {
                return;
            }
            delete this._publishers[roomName][pub.getStreamName()];
            return pub.leave();
        }

        const pubList = this.getRoomsPublisherList().filter((pub) => pub.getStreamName() === streamName);
        pubList.forEach((pub) => {
            pub.leave();
            delete this._publishers[pub.getRoomName()][pub.getStreamName()];
        });
    }

    public leaveAllRoom() {
        this.getRoomNameList().forEach((roomName) => {
            this._rooms[roomName].leave();
        });
        this.getRoomsPublisherList().forEach((pub) => {
            pub.leave();
        });
        this._rooms = {};
        this._publishers = {};
    }

    public donWhenRoomJoined(room: Room): Promise<void> {
        return new Promise((resolve) => {
            if (room.getStatus() === RoomStatus.IN) {
                resolve();
            } else {
                room.once('status:in', () => resolve());
            }
        });
    }

    public inRoom(roomName: string): boolean {
        return !!this._rooms[roomName];
    }

    public getRoomNameList(): string[] {
        return Object.keys(this._rooms);
    }

    public getRoomMemberIdList(roomName: string): string[] {
        if (this._rooms[roomName]) {
            return this._rooms[roomName].getMemberIdList();
        }
        return [];
    }

    // 
    public getRoomPublisherList(roomName: string) {
        if (this._publishers[roomName]) {
            return Object.keys(this._publishers[roomName]).map((name) => {
                return this._publishers[roomName][name];
            });
        }
        return [];
    }

    // 
    public getRoomsPublisherList() {
        const roomNames = Object.keys(this._publishers);
        let pubList: StreamPublisher[] = [];
        roomNames.forEach((roomName) => {
            const pubs = this.getRoomPublisherList(roomName);
            pubList = pubList.concat(pubs);
        });
        return pubList;
    }

    public getRoomMemberNameList(roomName: string): string[] {
        if (this._rooms[roomName]) {
            return this._rooms[roomName].getMemberNameList();
        }
        return [];
    }

    public getRoomMediaStats(roomName: string): MediaStats | null {
        if (this._rooms[roomName]) {
            return this._rooms[roomName].getMediaStats();
        }
        return null;
    }

    // 
    private _sendMicphoneOpenStatus(isOpen: boolean) {
        const status = isOpen ? 0 : 1; // 0 1
        this.getRoomNameList().forEach((roomName) => {
            this._rooms[roomName]
                .sendInternalMessage({
                    request: 'common_status',
                    event_type: 0,
                    status: status
                })
                .catch(() => {
                    /** noop */
                });
        });
    }

    // 
    private _sendCameraOpenStatus(isOpen: boolean) {
        const op = isOpen ? 1 : 0; // 1 0
        this.getRoomNameList().forEach((roomName) => {
            this._rooms[roomName]
                .sendInternalMessage({
                    request: 'avinput_status',
                    media_type: 2,
                    op: op
                })
                .catch(() => {
                    /** noop */
                });
        });
    }

    private _attachScreenMediaEvents() {
        // 
        this._screenMedia.on('has-stream', () => {
            const roomNames = Object.keys(this._rooms);
            roomNames.forEach((roomName) => {
                // const pub = this._publishers[roomName]?.share;
                const room = this._rooms[roomName];
                // if (pub) {
                //     return pub.publish();
                // }

                this.donWhenRoomJoined(room)
                    .then(() => {
                        return this._screenMedia.requestStream();
                    })
                    .then((stream) => {
                        console.log('stream before publish', stream);
                        const pub = new StreamPublisher(roomName, 'share', stream, this._commonData, this._janusManager);
                        this._publishers[roomName]['share'] = pub;
                        pub.setShareType(PublishType.Coustom);
                        pub.publish();
                    });
            });
        });

        // 
        this._screenMedia.on('remove-stream', () => {
            const roomNames = Object.keys(this._publishers);
            roomNames.forEach((roomName) => {
                this._publishers[roomName]?.share?.unpublish();
            });
        });
    }

    private _attachLocalMediaEvents() {
        this._localMedia.on('has-stream', (stream: MediaStream) => {
            const videoTracks = stream.getVideoTracks();
            const audioTracks = stream.getAudioTracks();

            // 
            if (videoTracks.length !== 0) {
                this._sendCameraOpenStatus(true);
            }
            if (audioTracks.length !== 0) {
                this._sendMicphoneOpenStatus(true);
            }

            this.getRoomNameList().forEach((roomName) => {
                this._rooms[roomName].publish();
            });
        });

        this._localMedia.on('remove-stream', () => {
            this.getRoomNameList().forEach((roomName) => {
                this._rooms[roomName].unpublish();
            });
        });

        this._localMedia.on('pause-video', () => {
            this._sendCameraOpenStatus(false);
        });

        this._localMedia.on('pause-audio', () => {
            this._sendMicphoneOpenStatus(false);
        });

        this._localMedia.on('resume-audio', () => {
            this._sendMicphoneOpenStatus(true);
        });

        this._localMedia.on('resume-video', () => {
            this._sendCameraOpenStatus(true);
        });
    }

    private _attachRoomEvents(room: Room) {
        if (!room) {
            return;
        }
        const roomName = room.getName();

        room.on('member-join', roomName, (userId: string) => {
            const user = this._userControl.getUser(userId);
            if (user) {
                const name = user.getName();
                this.emit('member-join:' + roomName + ':' + name, roomName, name);
            }
        });

        room.on('other:member-join', roomName, (userId: string) => {
            const user = this._userControl.getSubscriber(userId);
            if (!user) {
                return;
            }
            const streamId = user.getName(); // _h5_xxxuserId
            const userName = streamId.replace(/_h5_.*$/, '');
            const streamName = user.getStreamName();
            if (user.getStreamType() === StreamType.Share) {
                this.emit('other-share-start:' + roomName + ':' + userName, roomName, userName);
            }
            if (user.getStreamType() === StreamType.Other) {
                this.emit('custom-stream-start:' + roomName + ':' + userName, roomName, userName, streamName);
            }
        });

        room.on('member-leave', roomName, (userId: string) => {
            const user = this._userControl.getUser(userId);
            if (user) {
                const name = user.getName();
                this.emit('member-leave:' + roomName + ':' + name, roomName, name);
            }
        });

        room.on('other:member-leave', roomName, (userId: string) => {
            const user = this._userControl.getSubscriber(userId);
            if (!user) {
                return;
            }
            const streamId = user.getName();
            const userName = streamId.replace(/_h5_.*$/, '');
            const streamName = user.getStreamName();
            if (user.getStreamType() === StreamType.Share) {
                this.emit('other-share-stop:' + roomName + ':' + userName, roomName, userName);
            }
            if (user.getStreamType() === StreamType.Other) {
                this.emit('custom-stream-stop:' + roomName + ':' + userName, roomName, userName, streamName);
            }
        });

        room.on('update', roomName, (userIdList: string[]) => {
            this.emit('update:' + roomName, roomName, userIdList);
        });

        room.on('status:in', roomName, () => {
            this.emit('join:' + roomName, roomName);
        });

        room.on('status:out', roomName, () => {
            this.emit('leave:' + roomName, roomName);
            room.releaseGroup(roomName);

            // 
            const isLeavedAllRoom = Object.keys(this._rooms).length === 0;
            const isAllUserMuted = this._userControl.isAllMute();
            if (isLeavedAllRoom && isAllUserMuted) {
                this._userControl.setAllMute(false);
            }
        });

        room.on('media-stats-update', roomName, (r: MediaStats) => {
            this.emit('media-stats-update:' + roomName, roomName, r);
        });

        room.on('ym_event_common_status', roomName, (msg: YoumeCommonStatusEvent) => {
            const { user_id: userId, event_type, status } = msg;
            if (/_h5_.*$/.test(userId)) {
                return;
            }
            if (event_type === YoumeCommonStatusEventType.MIC_STATUS) {
                if (status === 0) {
                    this.emit('others-mic-on:', roomName, userId);
                } else {
                    this.emit('others-mic-off:', roomName, userId);
                }
            }
            if (event_type === YoumeCommonStatusEventType.SPEAKER_STATUS) {
                if (status === 0) {
                    this.emit('others-speaker-on:', roomName, userId);
                } else {
                    this.emit('others-speaker-off:', roomName, userId);
                }
            }
        });

        // ()
        room.on('ym_event_join_leave_notify', roomName, (msg: YoumeJoinLeaveUser[]) => {
            const users: YoumeMemberChangeUser[] = msg.map((u) => ({
                userId: u.user_id.replace(/_h5_.*$/, ''),
                roomId: roomName,
                isJoin: u.flag === 1
            }));
            // _h5_xxxid
            const result = users.filter((u) => {
                const isUserExist = !!this._roomUsers[u.roomId] && !!this._roomUsers[u.roomId][u.userId];
                if (u.isJoin && !isUserExist) {
                    if (this._roomUsers[u.roomId]) {
                        this._roomUsers[u.roomId][u.userId] = u.userId;
                    } else {
                        this._roomUsers[u.roomId] = { [u.userId]: u.userId };
                    }
                    return true;
                }
                if (!u.isJoin && isUserExist) {
                    delete this._roomUsers[u.roomId][u.userId];
                    const len = Object.keys(this._roomUsers[u.roomId]).length;
                    if (len === 0) {
                        delete this._roomUsers[u.roomId];
                    }
                    return true;
                }
                return false;
            });

            if (result.length) {
                this.emit('member-change:', roomName, result);
            }
        });

        // 
        room.on('ym_event_channel_user_list_notify', roomName, (msg: YoumeJoinLeaveUser[]) => {
            this._roomUsers = {};
            // _h5_xxx
            const temp: { [uid: string]: YoumeMemberChangeUser } = {};
            const users: YoumeMemberChangeUser[] = msg.map((u) => ({
                userId: u.user_id.replace(/_h5_.*$/, ''),
                roomId: roomName,
                isJoin: true
            }));
            users.forEach((u) => {
                temp[u.userId] = u;
            });

            const result = Object.keys(temp).map((uid) => temp[uid]);
            result.forEach((u) => {
                if (this._roomUsers[u.roomId]) {
                    this._roomUsers[u.roomId][u.userId] = u.userId;
                } else {
                    this._roomUsers[u.roomId] = { [u.userId]: u.userId };
                }
            });
            if (result.length) {
                this.emit('member-change:', roomName, result);
            }
        });

        // 
        room.on('ym_event_avinput_stat_notify', roomName, (msg: YoumeAvInputStatNotifyEvent) => {
            const { user_id: userId, media_type: mediaType, op } = msg;
            if (/_h5_.*$/.test(userId)) {
                return;
            }
            if (mediaType === 2 && op === 1) {
                this.emit('others-video-input-start:', roomName, userId);
            }
            if (mediaType === 2 && op === 0) {
                this.emit('others-video-input-stop:', roomName, userId);
            }
        });

        // 
        room.on('ym_message', roomName, (msg: YoumeMessageEvent) => {
            const content: YoumeOnReceiveMessage = {
                fromUserId: msg.from_user_id,
                msg: msg.msg_content,
                roomId: roomName
            };
            if (/_h5_.*$/.test(content.fromUserId)) {
                return;
            }
            this.emit('on-recive-message:', roomName, content);
        });
    }
}
