import WildEmitter from 'wildemitter';
import Janus from '../lib/janus';
import { GATEWAY_URL, GATEWAY_URL_DEV } from './config';
import { CommonData } from './common-data';
import { JanusHandle } from './janus-handle';

export const DISCONNECTED = 'disconnected';
export const CONNECTING = 'connecting';
export const CONNECTED = 'connected';
export const ERROR = 'error';
export const ENDED = 'ended';
export const RECONNETING = 'reconnecting';

export type VideoConfig =
    | false
    | 'screen'
    | 'window'
    | 'lowres' /* 320x240 */
    | 'lowres-16:9' /* 320x180 */
    | 'stdres' /* 640x480 */
    | 'stdres-16:9' /* 640x360 */
    | 'hires'
    | 'hires-16:9'
    | 'hdres' /*  1280x7204:3 */
    | 'fhdres' /* 1920x1080 */
    | '4kres'; /* 3840x2160 */

export type AudioConfig = false | 'screen' | 'mic' | true;

export interface MediaConfig {
    video?: VideoConfig;
    audio?: AudioConfig;
    stream?: MediaStream;
    electronScreenSourceId?: string;
    electronScreenShareMaxFrameRate?: number;
    electronScreenShareMaxWidth?: number;
    electronScreenShareMaxHeight?: number;
    bitrate?: number;
}

export class JanusManager extends WildEmitter {
    private _janus: Janus | null = null;

    private _status: string = DISCONNECTED;

    private _localJsep: any = null;
    private _remoteJsep: any = null;
    private _resolveLocalJsep: Array<(jsep: any) => void> = [];
    private _resolveRemoteJsep: Array<(jsep: any) => void> = [];

    //  handle 
    private _defaultJanusHandle: Janus.IJanusPluginHandle | null = null;
    // 
    private _maxReconnectTryCount = 5;
    private _reconnectTryCount = 0;

    constructor(private _commonData: CommonData) {
        super();
    }

    private generateServer(): string {
        // appkeyappkeywebsocket
        const appkey = this._commonData.get('mainAppKey') || this._commonData.get('appKey');
        const serverSuffix = appkey.substring(appkey.length - 8).toLocaleLowerCase();
        const wssServer = 'wss://ymrtc-' + serverSuffix + GATEWAY_URL;
        return this._commonData.get('dev') ? GATEWAY_URL_DEV : wssServer;
    }

    public reconnect(): Promise<void> {
        // , 
        let retryInterval = 1000;
        return new Promise((resolve, reject) => {
            const reclaim = () => {
                this._changeStatus(RECONNETING);
                setTimeout(() => {
                    this._janus!.reconnect({
                        server: this.generateServer(),
                        success: () => {
                            this._reconnectTryCount = 0;
                            retryInterval = 1000;
                            this._changeStatus(CONNECTED);
                            resolve();
                        },
                        error: () => {
                            if (this._reconnectTryCount <= this._maxReconnectTryCount) {
                                retryInterval += this._reconnectTryCount * 800;
                                this._reconnectTryCount++;
                                reclaim();
                            } else {
                                this._changeStatus(ERROR);
                                reject();
                            }
                        }
                    });
                }, retryInterval);
            };
            reclaim();
        });
    }

    public init(): Promise<void> {
        this._changeStatus(CONNECTING);
        return new Promise<void>((resolve) => {
            Janus.init({
                debug: this._commonData.get('debug'),
                callback: () => {
                    resolve();
                }
            });
        })
            .then(() => {
                return new Promise<void>((resolve, reject) => {
                    this._janus = new Janus({
                        server: this.generateServer(),
                        success: () => {
                            resolve();
                        },
                        error: (err) => {
                            // 
                            if (this._janus && !this.isConnected()) {
                                return this.reconnect();
                            }

                            this._janus = null;
                            this._defaultJanusHandle = null;
                            const e = new Error();
                            e.name = err;
                            this._changeStatus(ERROR);
                            reject(e);
                        },
                        destroyed: () => {
                            this._janus = null;
                            this._defaultJanusHandle = null;
                            this._changeStatus(ENDED);
                        }
                    });
                });
            })
            .then(() => {
                return new Promise<void>((resolve, reject) => {
                    this._janus!.attach({
                        plugin: 'janus.plugin.videoroom',
                        success: (handle) => {
                            this._defaultJanusHandle = handle;
                            this.emit('init');
                            this._changeStatus(CONNECTED);
                            resolve();
                        },
                        error: (err) => {
                            if (this._janus) {
                                this._janus.destroy();
                            }
                            this._defaultJanusHandle = null;
                            const e = new Error();
                            e.name = err;
                            this._changeStatus(ERROR);
                            reject(e);
                        },
                        consentDialog: (on) => {
                            // e.g., Darken the screen if on=true (getUserMedia incoming), restore it otherwise
                            this.emit('consentDialog', on);
                        },
                        onmessage: (msg, jsep) => {
                            // We got a message/event (msg) from the plugin
                            // If jsep is not null, this involves a WebRTC negotiation
                            this.emit('message', msg, jsep);
                        },
                        onlocalstream: (stream: MediaStream) => {
                            // We have a local stream (getUserMedia worked!) to display
                            this.emit('localStream', stream);
                        },
                        onremotestream: (stream: MediaStream) => {
                            // We have a remote stream (working PeerConnection!) to display
                            this.emit('remoteStream', stream);
                        },
                        ondataopen: () => {
                            this.emit('dataOpen');
                        },
                        ondata: (data) => {
                            this.emit('dataReceive', data);
                        },
                        webrtcState: (isActive) => {
                            this.emit('webrtcState', isActive);
                        },
                        iceState: (state) => {
                            this.emit('iceState', state);
                        },
                        mediaState: (data) => {
                            this.emit('mediaState', data);
                        },
                        slowLink: (data) => {
                            this.emit('slowLink', data);
                        },
                        oncleanup: () => {
                            // PeerConnection with the plugin closed, clean the UI
                            // The plugin handle is still valid so we can create a new one
                            this.emit('cleanup');
                        },
                        detached: () => {
                            // Connection with the plugin closed, get rid of its features
                            // The plugin handle is not valid anymore
                            this.emit('detached');
                        }
                    });
                });
            });
    }

    public destroy() {
        if (this._defaultJanusHandle) {
            this._defaultJanusHandle.detach();
        }
        if (this._janus) {
            this._janus.destroy();
        }
        this.emit('destroy');
    }

    public doWhenInited(): Promise<void> {
        return new Promise((resolve) => {
            if (this._janus && this._defaultJanusHandle) {
                resolve();
            } else {
                this.once('init', () => resolve());
            }
        });
    }

    public createSubHandle(): JanusHandle {
        const h = new JanusHandle();
        this.doWhenInited().then(() => {
            h.init(this._janus!);
        });
        return h;
    }

    public getServer(): string {
        return this._janus ? this._janus.getServer() : '';
    }

    public isConnected(): boolean {
        return !!this._janus && this._janus.isConnected();
    }

    public isReconnecting(): boolean {
        return this._status === RECONNETING;
    }

    public getSessionId(): string {
        return this._janus ? this._janus.getSessionId() : '';
    }

    public isAudioMuted(): boolean {
        return !!this._defaultJanusHandle && this._defaultJanusHandle.isAudioMuted();
    }

    public isVideoMuted(): boolean {
        return !!this._defaultJanusHandle && this._defaultJanusHandle.isVideoMuted();
    }

    public muteAudio() {
        this.doWhenInited().then(() => this._defaultJanusHandle!.muteAudio());
    }

    public unmuteAudio() {
        this.doWhenInited().then(() => this._defaultJanusHandle!.unmuteAudio());
    }

    public muteVideo() {
        this.doWhenInited().then(() => this._defaultJanusHandle!.muteVideo());
    }

    public unmuteVideo() {
        this.doWhenInited().then(() => this._defaultJanusHandle!.unmuteVideo());
    }

    public send(data: any) {
        this.doWhenInited().then(() => this._defaultJanusHandle!.send(data));
    }

    // ()
    public modifyRecordGain(gain: number) {
        if (this._defaultJanusHandle!.webrtcStuff) {
            this._defaultJanusHandle!.webrtcStuff.gainValue = gain;
            if (this._defaultJanusHandle!.webrtcStuff.gainNode) {
                this._defaultJanusHandle!.webrtcStuff.gainNode.gain.value = gain;
            } else {
                console.warn('webrtcStuff gainNode:' + this._defaultJanusHandle!.webrtcStuff.gainNode);
            }
        } else {
            console.warn('webrtcStuff:' + this._defaultJanusHandle!.webrtcStuff);
        }
    }

    public createOffer(mediaConfig: MediaConfig = {}): Promise<any> {
        const offerOptions: { media: any; stream?: any } = { media: {} };
        if (mediaConfig.stream) {
            offerOptions.stream = mediaConfig.stream;
        } else {
            offerOptions.media = Object.assign({}, mediaConfig, {
                audioRecv: false,
                videoRecv: false,
                audioSend: true,
                videoSend: this._commonData.get('video') || !!mediaConfig.video
            });
        }
        return this.doWhenInited().then(() => {
            return new Promise((resolve, reject) => {
                this._defaultJanusHandle!.createOffer(
                    Object.assign(
                        {},
                        {
                            success: (jsep: any) => {
                                this._localJsep = jsep;
                                this._resolveLocalJsep.forEach((f) => {
                                    f(jsep);
                                });
                                resolve(jsep);
                            },
                            error: (err: string | Error) => {
                                if (typeof err === 'string') {
                                    const e = new Error(err);
                                    e.name = 'OfferError';
                                    reject(e);
                                } else {
                                    reject(err);
                                }
                            }
                        },
                        offerOptions
                    )
                );
            });
        });
    }

    // sdpjanus peerConnection
    public handleRemoteJsep(data: { jsep: any }) {
        this.doWhenInited().then(() => this._defaultJanusHandle!.handleRemoteJsep(data));

        this._remoteJsep = data.jsep;
        this._resolveRemoteJsep.forEach((f) => {
            f(data.jsep);
        });
    }

    public hangup() {
        if (this._defaultJanusHandle) {
            this._defaultJanusHandle.hangup();
        }
    }

    public requestLocalJsep(): Promise<any> {
        if (this._localJsep) {
            return Promise.resolve(this._localJsep);
        }
        return new Promise((resolve) => {
            this._resolveLocalJsep.push(resolve);
        });
    }

    public requestRemoteJsep(): Promise<any> {
        if (this._remoteJsep) {
            return Promise.resolve(this._remoteJsep);
        }
        return new Promise((resolve) => {
            this._resolveRemoteJsep.push(resolve);
        });
    }

    private _changeStatus(status: string) {
        if (this._status !== status) {
            this._status = status;
            this.emit('status:' + status, status);
        }
    }

    public static isWebRTCSupported() {
        return Janus.isWebrtcSupported();
    }

    public static listDevices(callback: (devices: MediaDeviceInfo[]) => void, config?: any): void {
        Janus.listDevices(callback, config);
    }

    public static attachMediaStream(element: Element, stream: MediaStream): void {
        Janus.attachMediaStream(element, stream);
    }

    public static getBrowserDetails(): any {
        //@ts-ignore
        return Janus.webRTCAdapter.browserDetails;
    }
}
