import WildEmitter from 'wildemitter';
import { JanusManager } from './janus-manager';
import { JanusHandle } from './janus-handle';
import { JsonMap } from '../types/json';
import { MediaStats } from './get-stats';
import { CommonData } from './common-data';

export const SIGNALING_NEW = 'new';
export const SIGNALING_WAITING = 'waiting';
export const SIGNALING_NEGOTIATING = 'negotiating';
export const SIGNALING_NEGOTIATED = 'negotiated';
export const SIGNALING_FAILED = 'failed';

// 
export type MediaSubscribeConfig = 'all' | 'audio' | 'video';

export interface MediaSubscribePauseStatus {
    video: boolean; // 
    audio: boolean; // 
}

export enum StreamType {
    Normal = 0, // 
    Share = 1, // 
    Other = 2 // 
}

export class User extends WildEmitter {
    private readonly _userId: string;

    private readonly _userName: string;

    private readonly _roomId: string;

    private _streamType: StreamType = StreamType.Normal; // 

    private _streamName: string = ''; // , StreamType.Other

    private _audioMuted = false;

    private _iceState = 'new';

    private _stream: MediaStream | null = null;

    private _janusHandle: JanusHandle;

    private _isAudioPauseSubscribe: boolean = false; // 
    private _isVideoPauseSubscribe: boolean = false;
    private _status: string = SIGNALING_NEW;
    private _jsep: any = null;
    private _isRequestedStream: boolean = false;
    private _janusManager: JanusManager;
    private _iceRestartTimeout: NodeJS.Timeout | null = null;

    constructor(
        roomId: string,
        userId: string,
        userName: string,
        privateId: string,
        janus: JanusManager,
        private _commonData: CommonData
    ) {
        super();
        this._userId = userId;
        this._userName = userName;
        this._roomId = roomId;
        this._janusManager = janus;

        this._changeStatus(SIGNALING_NEGOTIATING);
        this._janusHandle = janus.createSubHandle();
        this._janusHandle.send({
            message: {
                request: 'join',
                room: roomId,
                ptype: 'subscriber',
                feed: userId,
                private_id: privateId,
                appkey: this._commonData.get('appKey')
            }
        });

        this._janusHandle.on('message', (msg: JsonMap, jsep: any) => {
            if (typeof jsep !== 'undefined' && jsep !== null) {
                if (msg.display !== 'undefined' && msg.display !== null) {
                    this._jsep = jsep;
                    if (this._isRequestedStream) {
                        this._janusHandle
                            .createAnswer(
                                {
                                    audioRecv: true,
                                    videoRecv: true,
                                    audioSend: false,
                                    videoSend: false
                                },
                                jsep
                            )
                            .then((jsep2) => {
                                this._janusHandle.send({
                                    message: {
                                        request: 'start',
                                        room: roomId,
                                        appkey: this._commonData.get('appKey')
                                    },
                                    jsep: jsep2
                                });
                                this._changeStatus(SIGNALING_NEGOTIATED);
                            });
                    }
                }
            }

            // 
            const paused = <string | undefined>msg['paused'];
            if (paused === 'ok') {
                this._isAudioPauseSubscribe = true;
                this._isVideoPauseSubscribe = true;
                this.emit('pause-subscribe', this._isAudioPauseSubscribe, this._isVideoPauseSubscribe);
            }

            // 
            const stared = <string | undefined>msg['started'];
            if (stared === 'ok') {
                this._isAudioPauseSubscribe = false;
                this._isVideoPauseSubscribe = false;
                this.emit('resume-subscribe', !this._isAudioPauseSubscribe, !this._isVideoPauseSubscribe);
            }
        });

        this._janusHandle.on('remoteStream', (stream: MediaStream) => {
            if (this._audioMuted) {
                this._janusHandle.muteAudio();
            }
            this._stream = stream;
            this.emit('update-stream', stream);
        });

        this._janusHandle.on('iceState', (state: string) => {
            this._iceState = state;
            this.emit('ice-status:' + state, state);

            if (state === 'connected') {
                if (this._iceRestartTimeout) {
                    clearTimeout(this._iceRestartTimeout);
                    this._iceRestartTimeout = null;
                }
            }

            //  ice restart
            if (state === 'failed' || state === 'disconnected') {
                if (this._janusManager.isConnected()) {
                    if (this._iceRestartTimeout) {
                        clearTimeout(this._iceRestartTimeout);
                        this._iceRestartTimeout = null;
                    }

                    // disconnectedconnected
                    this._iceRestartTimeout = setTimeout(() => {
                        this.iceRestart().catch(() => {
                            if (!this._janusManager.isReconnecting()) {
                                this._janusManager.reconnect().then();
                            }
                        });
                    }, 2500);
                }
                // ice
                this._janusManager.once('status:connected', () => {
                    if (this._iceState === 'disconnected' || this._iceState === 'failed') {
                        this.iceRestart().then();
                    }
                });
            }
        });

        this._janusHandle.on('stats-update', (r: MediaStats) => {
            this.emit('media-stats-update', r);
        });
    }

    public setStreamType(type: StreamType) {
        this._streamType = type;
    }

    public getStreamType() {
        return this._streamType;
    }

    public setStreamName(name: string) {
        this._streamName = name;
    }

    public getStreamName() {
        return this._streamName;
    }

    private iceRestart(): Promise<void> {
        return new Promise((resolve, reject) => {
            this._changeStatus(SIGNALING_NEGOTIATING);
            let timeoutId = setTimeout(() => {
                reject();
            }, 4000);

            this._janusHandle.send({
                message: {
                    request: 'configure',
                    restart: true
                },
                success: () => {
                    clearTimeout(timeoutId);
                    timeoutId = <any>null;
                    this._changeStatus(SIGNALING_NEGOTIATED);
                    resolve();
                },
                error: () => {
                    clearTimeout(timeoutId);
                    timeoutId = <any>null;
                    this._changeStatus(SIGNALING_FAILED);
                    reject();
                }
            });
        });
    }

    public getId() {
        return this._userId;
    }

    public getName() {
        return this._userName;
    }

    public getIceConnectionState() {
        return this._iceState;
    }

    public requestStream(): Promise<MediaStream> {
        if (this._stream) {
            return Promise.resolve(this._stream);
        }

        if (this._isAudioPauseSubscribe || this._isVideoPauseSubscribe) {
            this.resumeSubscribe().catch(() => {}); // 
        }

        return new Promise((resolve, reject) => {
            if (this._jsep) {
                this._janusHandle
                    .createAnswer(
                        {
                            audioRecv: true,
                            videoRecv: true,
                            audioSend: false,
                            videoSend: false
                        },
                        this._jsep
                    )
                    .then((jsep2) => {
                        this._janusHandle.send({
                            message: {
                                request: 'start',
                                room: this._roomId,
                                appkey: this._commonData.get('appKey')
                            },
                            jsep: jsep2
                        });
                        this._changeStatus(SIGNALING_NEGOTIATED);
                    });
            } else {
                this._isRequestedStream = true;
            }
            this.once('update-stream', (stream: MediaStream) => resolve(stream));
        });
    }

    // 
    public pauseSubscribe(param?: MediaSubscribeConfig): Promise<void> {
        return new Promise((resolve, reject) => {
            // 
            if (!this._stream) {
                return reject();
            }

            const isPauseAudio = param === 'audio';
            const isPauseVideo = param === 'video';
            const isPauseAll = !param || param === 'all';

            if (isPauseAll) {
                this._janusHandle.send({
                    message: {
                        request: 'pause'
                    },
                    success: () => {
                        resolve();
                    },
                    error: () => {
                        reject();
                    }
                });
            } else {
                const options: { video?: boolean; audio?: boolean } = {};
                if (isPauseVideo) options.video = false;
                if (isPauseAudio) options.audio = false;
                this._janusHandle.send({
                    message: {
                        request: 'configure',
                        ...options
                    },
                    success: () => {
                        if (isPauseAudio) this._isAudioPauseSubscribe = true;
                        if (isPauseVideo) this._isVideoPauseSubscribe = true;
                        this.emit('resume-subscribe', this._isAudioPauseSubscribe, this._isVideoPauseSubscribe);
                        resolve();
                    },
                    error: () => {
                        reject();
                    }
                });
            }
        });
    }

    // 
    public resumeSubscribe(param?: MediaSubscribeConfig): Promise<void> {
        return new Promise((resolve, reject) => {
            // 
            if (!this._stream) {
                return reject();
            }

            const isResumeAudio = param && param === 'audio';
            const isResumeVideo = param && param === 'video';
            const isResumeAll = !param || param === 'all';
            if (isResumeAll) {
                this._janusHandle.send({
                    message: {
                        request: 'start'
                    },
                    success: () => {
                        resolve();
                    },
                    error: () => {
                        reject();
                    }
                });
            } else {
                const options: { video?: boolean; audio?: boolean } = {};
                if (isResumeVideo) options.video = true;
                if (isResumeAudio) options.audio = true;
                this._janusHandle.send({
                    message: {
                        request: 'configure',
                        ...options
                    },
                    success: () => {
                        if (isResumeAudio) this._isAudioPauseSubscribe = false;
                        if (isResumeVideo) this._isVideoPauseSubscribe = false;
                        this.emit('resume-subscribe', !this._isAudioPauseSubscribe, !this._isVideoPauseSubscribe);
                        resolve();
                    },
                    error: () => {
                        reject();
                    }
                });
            }
        });
    }

    // 
    public getPauseSubscribeStatus(): MediaSubscribePauseStatus {
        return { video: this._isVideoPauseSubscribe, audio: this._isAudioPauseSubscribe };
    }

    public getMute(): boolean {
        return this._audioMuted;
    }

    public setMute(isMute: boolean) {
        this._audioMuted = isMute;
        if (this._stream) {
            let audioTracks: MediaStreamTrack[] = this._stream.getAudioTracks();
            audioTracks.forEach((element) => {
                element.enabled = !isMute;
            });
        }
    }

    public getMediaStats(): MediaStats {
        return this._janusHandle.getStats();
    }

    public destroy() {
        this._janusHandle.detach();
        this.emit('destroy');
    }

    private _changeStatus(status: string) {
        if (this._status !== status) {
            this._status = status;
            this.emit('signaling-status:' + status, status);
        }
    }
}
