    /**
     * 
     * @param mediaObject videoaudiohtml
     * @param deviceID id
     */
    public setOutputDevice(mediaObject: HTMLMediaElement, deviceID: string): boolean {
        // @ts-ignore
        if (!mediaObject.setSinkId) {
            console.warn('SetSinkId not supported');
            return false;
        }
        mediaObject
            // @ts-ignore
            .setSinkId(deviceID)
            .then(function () {
                console.log('Audio output device attached:', deviceID);
            })
            .catch(function (error: any) {
                console.error(error);
            });
        return true;
    }

