import WildEmitter from 'wildemitter';
import { CommonData } from './common-data';

export const MSG_LOGIN = 1;

export class Account extends WildEmitter {
    private _isLogin = false;

    /*private _logging = false;*/

    private _appKey = '';

    private _userId = '';

    private _token = '';

    constructor(private _commonData: CommonData) {
        super();
    }

    public login(appKey: string, userId: string, token: string): Promise<void> {
        if (this._isLogin) {
            if (userId !== this._userId) {
                this.logout();
            } else {
                return Promise.resolve();
            }
        }

        this._commonData.set('appKey', appKey);
        this._commonData.set('userId', userId);
        this._commonData.set('token', token);
        this._appKey = appKey;
        this._userId = userId;
        this._token = token;

        /*this._logging = true;*/
        this.emit('logging');
        this._isLogin = true;
        this.emit('login');

        // todo:  appKey 

        return Promise.resolve();
    }

    public logout() {
        this._isLogin = false;
        this._userId = '';
        this.emit('logout');
    }

    public isLogin() {
        return this._isLogin;
    }

    public doIfLogged(): Promise<void> {
        return new Promise((resolve, reject) => {
            if (this._isLogin) {
                resolve();
            } else {
                this.once('login', () => resolve());
            }
        });
    }

    public getMyUserId() {
        return this._userId;
    }

    public reconnect() {
        this._isLogin = false;
        /*this._logging = false;*/
        return this.login(this._appKey, this._userId, this._token);
    }
}
