import WildEmitter from 'wildemitter';
import { ENABLE_VIDEO_BY_DEFAULT } from './config';

export class CommonData extends WildEmitter {
    _data: { [key: string]: any } = {
        video: ENABLE_VIDEO_BY_DEFAULT,
        appKey: '',
        userId: '',
        token: '',
        debug: false,
        codec: 'h264,vp8',
        mainAppKey: '', // appKey, appkeyappkey
        videoNeedVoiceMode: 1, // 0,11
        localMediaConstraints: {
            video: {
                width: { ideal: 320 },
                frameRate: { ideal: 12 },
                facingMode: 'user'
            },
            audio: {
                autoGainControl: true,
                channelCount: 1,
                echoCancellation: true,
                noiseSuppression: true,
                sampleRate: { ideal: 8000 },
                sampleSize: { ideal: 8 },
                volume: 0.9
            }
        }
    };

    set(key: string, value: any) {
        this._data[key] = value;
        this.emit('change:' + key, key, value);
    }

    get(key: string) {
        return this._data[key];
    }
}
