import WildEmitter from 'wildemitter';
import { StreamType, User } from './user';
import { CommonData } from './common-data';
import { LocalMedia } from './local-media';
import { UserControl } from './user-control';
import { JanusManager } from './janus-manager';
import { JanusHandle } from './janus-handle';
import { JsonMap } from '../types/json';
import { MediaStats } from './get-stats';

export enum RoomStatus {
    OUT = 'out',
    JOINING = 'joining',
    IN = 'in'
}

export class Room extends WildEmitter {
    private _roomId = '';

    private _myPrivateId = '';

    private _status = RoomStatus.OUT;

    private _isPublishing = false;

    private _members: { [userId: string]: User } = {}; // 

    private _publishers: { [userId: string]: User } = {}; // 

    private _iceState = 'new';

    private _janusHandle: JanusHandle;

    private _iceRestartTimeout: NodeJS.Timeout | null = null;

    // 
    private _isChannelUeListNotifyEmitted: boolean = false;

    constructor(
        private _roomName: string,
        private _commonData: CommonData,
        private _localMedia: LocalMedia,
        private _userControl: UserControl,
        private _janusManager: JanusManager
    ) {
        super();
        this._roomId = _roomName;
        this._janusHandle = this._janusManager.createSubHandle();
        this.joinIn();

        this._janusHandle.on('message', (msg: JsonMap, jsep: any) => {
            let isMemberChanged = false;
            const event = <string | undefined>msg['videoroom'];

            if (typeof jsep !== 'undefined' && jsep !== null) {
                this._janusHandle.handleRemoteJsep(jsep);
            }
            if (event) {
                if (event === 'joined') {
                    this._myPrivateId = <string>msg['private_id'];
                    this._commonData.set('myId', <number>msg['id']);
                    this._changeStatus(RoomStatus.IN);
                    this.emit('joined');

                    const pubList = <JsonMap[] | undefined>msg['publishers'];
                    if (pubList) {
                        this.doWhenChannelUserListNotifyEmitted().then(() => {
                            pubList.forEach((f) => {
                                const userId = <string>f['id'];
                                const userName = <string>f['display'] || '' + userId;
                                const myUserId = this._commonData.get('userId');
                                // 
                                const isMe = myUserId === userName.replace(/_h5_.*$/, '');
                                if (isMe) {
                                    return;
                                }
                                const user = new User(
                                    this._roomId,
                                    userId,
                                    userName,
                                    this._myPrivateId,
                                    this._janusManager,
                                    this._commonData
                                );

                                const match = userName.match(/^.*_h5_(.*)$/);
                                const streamName = match ? match[1] : '';
                                user.setStreamName(streamName);
                                if (/^.*_h5_share$/.test(userName)) {
                                    user.setStreamType(StreamType.Share);
                                    this._publishers[userId] = user;
                                    this._userControl.registerSubscriber(user);
                                    this.emit('other:member-join', userId);
                                } else if (/^.*_h5_.*$/.test(userName)) {
                                    user.setStreamType(StreamType.Other);
                                    this._publishers[userId] = user;
                                    this._userControl.registerSubscriber(user);
                                    this.emit('other:member-join', userId);
                                } else {
                                    this._members[userId] = user;
                                    this._userControl.registerUser(user);
                                    this.emit('member-join', userId);
                                }
                            });
                            isMemberChanged = true;
                        });
                    }
                }

                if (event === 'event') {
                    const pubList = <JsonMap[] | undefined>msg['publishers'];
                    if (pubList) {
                        this.doWhenChannelUserListNotifyEmitted().then(() => {
                            pubList.forEach((f) => {
                                const userId = <string>f['id'];
                                const userName = <string>f['display'] || '' + userId;
                                const myUserId = this._commonData.get('userId');
                                const isMe = myUserId === userName.replace(/_h5_.*$/, '');
                                // ,
                                if (this._members[userId] || this._publishers[userId] || isMe) {
                                    return;
                                }
                                const user = new User(
                                    this._roomId,
                                    userId,
                                    userName,
                                    this._myPrivateId,
                                    this._janusManager,
                                    this._commonData
                                );

                                const match = userName.match(/^.*_h5_(.*)$/);
                                const streamName = match ? match[1] : '';
                                user.setStreamName(streamName);
                                if (/^.*_h5_share$/.test(userName)) {
                                    user.setStreamType(StreamType.Share);
                                    this._publishers[userId] = user;
                                    this._userControl.registerSubscriber(user);
                                    this.emit('other:member-join', userId);
                                } else if (/^._h5_.*$/.test(userName)) {
                                    user.setStreamType(StreamType.Other);
                                    this._publishers[userId] = user;
                                    this._userControl.registerSubscriber(user);
                                    this.emit('other:member-join', userId);
                                } else {
                                    this._members[userId] = user;
                                    this._userControl.registerUser(user);
                                    this.emit(`member-join`, userId);
                                }
                            });
                        });
                    }

                    const leaving = <string | undefined>msg['leaving'];
                    if (leaving === 'ok') {
                        // 
                        this._janusHandle.detach();
                        this._janusHandle.off('message');
                        this.clear();
                        isMemberChanged = true;
                        this._changeStatus(RoomStatus.OUT);
                    }
                    if (leaving && this._members[leaving]) {
                        this.emit('member-leave', leaving);
                        this._members[leaving].destroy();
                        delete this._members[leaving];
                        isMemberChanged = true;
                    }

                    if (leaving && this._publishers[leaving]) {
                        const pub = this._publishers[leaving];
                        if (pub.getStreamType() === StreamType.Share) {
                            this.emit('other:member-leave', leaving);
                        }
                        if (pub.getStreamType() === StreamType.Other) {
                            this.emit('other:member-leave', leaving);
                        }
                        this._publishers[leaving].destroy();
                        delete this._publishers[leaving];
                    }

                    const unPub = <string | undefined>msg['unpublished'];
                    if (unPub && this._members[unPub]) {
                        this.emit('member-leave', unPub);
                        this._members[unPub].destroy();
                        delete this._members[unPub];
                        isMemberChanged = true;
                    }

                    if (unPub && this._publishers[unPub]) {
                        const pub = this._publishers[unPub];
                        if (pub.getStreamType() === StreamType.Share) {
                            this.emit('other:member-leave', unPub);
                        }
                        if (pub.getStreamType() === StreamType.Other) {
                            this.emit('other:member-leave', unPub);
                        }
                        this._publishers[unPub].destroy();
                        delete this._publishers[unPub];
                    }
                }

                // , 
                if (event === 'ym_event_common_status' && msg['body']) {
                    this.doWhenChannelUserListNotifyEmitted().then(() => {
                        this.emit('ym_event_common_status', msg['body']);
                    });
                }

                // 
                if (event === 'ym_event_avinput_stat_notify' && msg['body']) {
                    this.doWhenChannelUserListNotifyEmitted().then(() => {
                        this.emit('ym_event_avinput_stat_notify', msg['body']);
                    });
                }

                // 
                if (event === 'ym_event_join_leave_notify' && msg['user_list']) {
                    this.doWhenChannelUserListNotifyEmitted().then(() => {
                        this.emit('ym_event_join_leave_notify', msg['user_list']);
                    });
                }

                // 
                if (event === 'ym_event_channel_user_list_notify' && msg['user_list']) {
                    this._isChannelUeListNotifyEmitted = true;
                    this.emit('ym_event_channel_user_list_notify', msg['user_list']);
                }

                // 
                if (event === 'ym_message' && msg['body']) {
                    this.doWhenChannelUserListNotifyEmitted().then(() => {
                        this.emit('ym_message', msg['body']);
                    });
                }

                if (isMemberChanged) {
                    this.emit('update', this.getMemberNameList());
                }
            }
        });

        this._janusHandle.on('stats-update', (r: MediaStats) => {
            this.emit('media-stats-update', r);
        });

        this._janusHandle.on('iceState', (state: string) => {
            this._iceState = state;
            // ice,iceRestart
            if (state === 'connected' && this._iceRestartTimeout) {
                clearTimeout(this._iceRestartTimeout);
                this._iceRestartTimeout = null;
            }

            // ice restart
            if (state === 'failed' || state === 'disconnected') {
                if (this._janusManager.isConnected()) {
                    if (this._iceRestartTimeout) {
                        clearTimeout(this._iceRestartTimeout);
                        this._iceRestartTimeout = null;
                    }

                    // disconnectedconnected
                    this._iceRestartTimeout = setTimeout(() => {
                        this.iceRestart().catch(() => {
                            if (!this._janusManager.isReconnecting()) {
                                this._janusManager.reconnect().catch(() => {
                                    /** noop */
                                });
                            }
                        });
                    }, 2500);
                }

                // iceiceRestart
                this._janusManager.once('status:connected', () => {
                    if (this._iceState === 'disconnected' || this._iceState === 'failed') {
                        this.iceRestart().catch(() => {
                            /** noop */
                        });
                    }
                });
            }
        });
    }

    public getName(): string {
        return this._roomName;
    }

    public joinIn() {
        const codec = this._commonData.get('codec');
        const videoNeedVoiceMode = this._commonData.get('videoNeedVoiceMode');

        this._janusHandle.send({
            message: {
                request: 'joinroom',
                room: this._roomName,
                ptype: 'publisher',
                display: this._commonData.get('userId'),
                appkey: this._commonData.get('appKey'),
                apptoken: this._commonData.get('token'),
                create: {
                    request: 'create',
                    room: this._roomName,
                    description: this._roomName,
                    publishers: 20,
                    is_private: false,
                    video_need_voice_mode: videoNeedVoiceMode,
                    videocodec: codec,
                    fir_freq: 1,
                    appkey: this._commonData.get('appKey'),
                    apptoken: this._commonData.get('token'),
                    display: this._commonData.get('userId'),
                    admin_key: 'supersecret'
                }
            },
            success: (data: JsonMap) => {
                const err = data && data['error_code'];
                if (err) {
                    const e = new Error();
                    e.name = err + '';
                    this.emit('join-error', err);
                }
            },
            error: (err: string) => {
                const e = new Error();
                e.name = err;
                this.emit('join-error', err);
            }
        });
        this._changeStatus(RoomStatus.JOINING);
    }

    // 
    public sendInternalMessage(msg: JsonMap): Promise<void> {
        return new Promise((resolve, reject) => {
            this._janusHandle.send({
                message: msg,
                success: () => {
                    resolve();
                },
                error: () => {
                    reject();
                }
            });
        });
    }

    // member-change
    private doWhenChannelUserListNotifyEmitted(): Promise<void> {
        return new Promise((resolve) => {
            if (this._isChannelUeListNotifyEmitted) {
                return resolve();
            }

            this.once('ym_event_channel_user_list_notify', () => {
                resolve();
            });
        });
    }

    private iceRestart(): Promise<void> {
        return new Promise((resolve, reject) => {
            const videoSend = this._commonData.get('video');
            this._janusHandle.createOfferIceRestart(videoSend).then((jsep) => {
                let timeoutId = setTimeout(() => {
                    reject();
                }, 4000);
                this._janusHandle.send({
                    message: {
                        request: 'configure',
                        refresh: true
                    },
                    jsep: jsep,
                    success: () => {
                        clearTimeout(timeoutId);
                        timeoutId = <any>null;
                        resolve();
                    },
                    error: () => {
                        clearTimeout(timeoutId);
                        timeoutId = <any>null;
                        reject();
                    }
                });
            });
        });
    }

    public publish() {
        if (this._isPublishing) {
            return;
        }
        this._isPublishing = true;
        new Promise<void>((resolve) => {
            if (this._status === RoomStatus.IN) {
                resolve();
            } else {
                this.once(`status:${RoomStatus.IN}`, () => resolve());
            }
        })
            .then(() => {
                if (!this._isPublishing) {
                    return Promise.resolve(null);
                }
                return this._localMedia.requestStream();
            })
            .then((stream: MediaStream | null) => {
                if (!stream) {
                    return null;
                }
                return this._janusHandle.createOffer(stream);
            })
            .then((jsep: any) => {
                if (!jsep) {
                    return;
                }
                this._janusHandle.send({
                    message: {
                        request: 'configure',
                        video: true,
                        audio: true,
                        bitrate: this._commonData.get('bitrate'),
                        appkey: this._commonData.get('appKey')
                    },
                    jsep: jsep
                });
            });
    }

    public unpublish() {
        this._isPublishing = false;
        this._janusHandle.send({
            message: {
                request: 'unpublish',
                appkey: this._commonData.get('appKey')
            }
        });
        this._janusHandle.hangup();
    }

    public leave() {
        this._janusHandle.send({
            message: {
                request: 'leave',
                appkey: this._commonData.get('appKey')
            }
        });
    }

    // 
    public clear() {
        this.getMemberIdList().forEach((id) => {
            this._members[id].destroy();
            delete this._members[id];
        });

        this.getPublisherIdList().forEach((id) => {
            this._publishers[id].destroy();
            delete this._publishers[id];
        });
    }

    public getMembers() {
        return this._members;
    }

    public getMemberIdList(): string[] {
        return Object.keys(this._members);
    }

    public getPublisherIdList(): string[] {
        return Object.keys(this._publishers);
    }

    public getMemberNameList(): string[] {
        return this.getMemberIdList().map((id) => {
            return this._members[id].getName();
        });
    }

    public getPublisherNameList(): string[] {
        return this.getPublisherIdList().map((id) => {
            return this._publishers[id].getName();
        });
    }

    public getStatus(): string {
        return this._status;
    }

    public getMediaStats(): MediaStats {
        return this._janusHandle.getStats();
    }

    private _changeStatus(status: RoomStatus) {
        if (this._status !== status) {
            this._status = status;
            this.emit('status:' + status, status);
        }
    }
}
