import WildEmitter from 'wildemitter';
import { JanusHandle } from './janus-handle';
import { JanusManager } from './janus-manager';
import { JsonMap } from '../types/json';
import { CommonData } from './common-data';

export enum PublisherJoinStatus {
    OUT = 'out',
    JOING = 'joing',
    IN = 'in'
}

export enum PublishType {
    Share = 0, // 
    Coustom = 1 // 
}

export class StreamPublisher extends WildEmitter {
    private readonly _roomName: string;

    private _shareType: PublishType = PublishType.Share;

    private _janusHandle: JanusHandle;

    private readonly _janusManager: JanusManager;

    private readonly _commonData: CommonData;

    private _status: PublisherJoinStatus = PublisherJoinStatus.OUT; // 

    private _isPublishing = false; // 

    private _streamName: string; // 

    private _stream: MediaStream;

    constructor(roomName: string, streamName: string, stream: MediaStream, commonData: CommonData, janusManager: JanusManager) {
        super();
        this._roomName = roomName;
        this._janusManager = janusManager;
        this._commonData = commonData;
        this._janusHandle = this._janusManager.createSubHandle();
        this._streamName = streamName;
        this._stream = stream;

        console.log('stream in new Publisher', stream);
        this._janusHandle.on('message', (msg: JsonMap, jsep: any) => {
            const event = <string | undefined>msg['videoroom'];
            if (typeof jsep !== 'undefined' && jsep !== null) {
                this._janusHandle.handleRemoteJsep(jsep);
            }

            if (event === 'joined') {
                this._changeStatus(PublisherJoinStatus.IN);
                this.emit('joined');
            }
        });

        this._changeStatus(PublisherJoinStatus.JOING);
        this.join().catch(() => {
            this._changeStatus(PublisherJoinStatus.OUT);
        });
    }

    private _changeStatus(status: PublisherJoinStatus) {
        if (this._status !== status) {
            this._status = status;
            this.emit('status:' + status, status);
        }
    }

    public join(): Promise<void> {
        return new Promise((resolve, reject) => {
            // 
            const name = this._commonData.get('userId') + '_h5_' + this._streamName;
            this._janusHandle.send({
                message: {
                    request: 'join',
                    room: this._roomName,
                    display: name,
                    ptype: 'publisher',
                    appkey: this._commonData.get('appKey')
                },
                success: () => {
                    resolve();
                },
                error: (err: any) => {
                    reject(err);
                }
            });
        });
    }

    public doWhenJoinRoom(): Promise<void> {
        return new Promise((resolve) => {
            if (this._status === PublisherJoinStatus.IN) {
                return resolve();
            }
            this.once('joined', () => {
                resolve();
            });
        });
    }

    public publish() {
        if (this._isPublishing || !this._stream) {
            return;
        }

        this._isPublishing = true;
        this.doWhenJoinRoom()
            .then(() => {
                return this._janusHandle.createOffer(this._stream);
            })
            .then((jsep: any) => {
                if (!jsep) {
                    return;
                }
                this._janusHandle.send({
                    message: {
                        request: 'configure',
                        audio: true,
                        video: true,
                        appkey: this._commonData.get('appKey')
                    },
                    jsep
                });
            });
    }

    public unpublish() {
        this._isPublishing = false;
        this._janusHandle.send({
            message: {
                request: 'unpublish',
                appkey: this._commonData.get('appKey')
            }
        });
        this._janusHandle.hangup();
    }

    public leave() {
        this._janusHandle.send({
            message: {
                request: 'leave',
                appkey: this._commonData.get('appKey')
            }
        });
    }

    public getRoomName() {
        return this._roomName;
    }

    public setShareType(type: PublishType) {
        this._shareType = type;
    }

    public getShareType() {
        return this._shareType;
    }

    public getStreamName() {
        return this._streamName;
    }

    public setStreamName(streamName: string) {
        this._streamName = streamName;
    }

    public getStream() {
        return this._stream;
    }

    public setStream(stream: MediaStream) {
        this._stream = stream;
    }
}
