import WildEmitter from 'wildemitter';
import Janus from '../lib/janus';
import { GetStats, MediaStats } from './get-stats';

export class JanusHandle extends WildEmitter {
    private _handle: Janus.IJanusPluginHandle | null = null;

    private _getStats: GetStats = new GetStats();

    private _sendOrRecv: 'send' | 'recv' | 'both' | 'unknown' = 'unknown';

    private _error: any = null;

    public init(janus: Janus) {
        janus.attach({
            plugin: 'janus.plugin.videoroom',
            success: (h) => {
                // save the janus handle
                this._handle = h;
                this.emit('init');
            },
            error: (err) => {
                this._error = err;
                this.emit('error', err);
            },
            consentDialog: (on) => {
                // e.g., Darken the screen if on=true (getUserMedia incoming), restore it otherwise
                this.emit('consentDialog', on);
            },
            onmessage: (msg, jsep) => {
                // We got a message/event (msg) from the plugin
                // If jsep is not null, this involves a WebRTC negotiation
                this.emit('message', msg, jsep);
            },
            onlocalstream: (stream: MediaStream) => {
                // Invoked after createOffer
                // This is our video, we have a local stream (getUserMedia worked!) to display
                this.emit('localStream', stream);
                this._sendOrRecv = 'send';
                this._getStats.setPC(this._handle!.webrtcStuff.pc);
            },
            onremotestream: (stream: MediaStream) => {
                // Invoked after handleRemoteJsep has got us a PeerConnection
                // This is the remote video, we have a remote stream (working PeerConnection!) to display
                this.emit('remoteStream', stream);
                this._sendOrRecv = 'recv';
                this._getStats.setPC(this._handle!.webrtcStuff.pc);
            },
            ondataopen: () => {
                this.emit('dataOpen');
            },
            ondata: (data) => {
                this.emit('dataReceive', data);
            },
            webrtcState: (isActive) => {
                this.emit('webrtcState', isActive);
            },
            iceState: (state) => {
                this.emit('iceState', state);
            },
            mediaState: (data) => {
                this.emit('mediaState', data);
            },
            slowLink: (data) => {
                this.emit('slowLink', data);
            },
            oncleanup: () => {
                // PeerConnection with the plugin closed, clean the UI
                // The plugin handle is still valid so we can create a new one
                this.emit('cleanup');
            },
            detached: () => {
                // Connection with the plugin closed, get rid of its features
                // The plugin handle is not valid anymore
                this.emit('detached');
            }
        });

        this._getStats.on('stats', (res: { send: MediaStats; recv: MediaStats }) => {
            this.emit('stats-update', this._sendOrRecv === 'send' ? res.send : res.recv);
        });
    }

    public doWhenInited(): Promise<void> {
        return new Promise((resolve, reject) => {
            if (this._handle) {
                resolve();
            } else if (this._error) {
                reject(this._error);
            } else {
                this.once('init', () => resolve());
                this.once('error', (err: any) => reject(err));
            }
        });
    }

    public send(data: any) {
        this.doWhenInited().then(() => this._handle!.send(data));
    }

    public createOffer(stream: MediaStream): Promise<any> {
        console.log('----------> stream in createOffer()', stream);
        return this.doWhenInited().then(() => {
            return new Promise((resolve, reject) => {
                this._handle!.createOffer({
                    media: {
                        audioRecv: false,
                        videoRecv: false,
                        audioSend: true,
                        videoSend: true
                    },
                    stream: stream,
                    success: (jsep) => {
                        resolve(jsep);
                    },
                    error: (err) => {
                        const e = new Error();
                        e.name = err;
                        reject(e);
                    }
                });
            });
        });
    }

    public createOfferIceRestart(videoSend: boolean): Promise<any> {
        // DOC: https://github.com/meetecho/janus-gateway/pull/753
        return this.doWhenInited().then(() => {
            return new Promise((resolve, reject) => {
                this._handle!.createOffer({
                    iceRestart: true,
                    media: {
                        audioRecv: false,
                        videoRecv: false,
                        audioSend: true,
                        videoSend: videoSend
                    },
                    success: (jsep) => {
                        resolve(jsep);
                    },
                    error: (err) => {
                        const e = new Error();
                        e.name = err;
                        reject(e);
                    }
                });
            });
        });
    }

    public createAnswer(media: Janus.IOfferMediaOptions, jsep: any): Promise<any> {
        return this.doWhenInited().then(() => {
            return new Promise((resolve, reject) => {
                this._handle!.createAnswer({
                    media: media,
                    success: (jsep) => {
                        resolve(jsep);
                    },
                    error: (err) => {
                        reject(err);
                    },
                    jsep
                });
            });
        });
    }

    public handleRemoteJsep(jsep: any) {
        this.doWhenInited().then(() => this._handle!.handleRemoteJsep({ jsep }));
    }

    public hangup() {
        if (this._handle) {
            this._handle.hangup();
            this._getStats.destroy();
        }
    }

    public isAudioMuted(): boolean {
        return !!this._handle && this._handle.isAudioMuted();
    }

    public isVideoMuted(): boolean {
        return !!this._handle && this._handle.isVideoMuted();
    }

    public muteAudio() {
        this.doWhenInited().then(() => this._handle!.muteAudio());
    }

    public unmuteAudio() {
        this.doWhenInited().then(() => this._handle!.unmuteAudio());
    }

    public muteVideo() {
        this.doWhenInited().then(() => this._handle!.muteVideo());
    }

    public unmuteVideo() {
        this.doWhenInited().then(() => this._handle!.unmuteVideo());
    }

    public getStats(): MediaStats {
        const s = this._getStats.getStats();
        return this._sendOrRecv === 'send' ? s.send : s.recv;
    }

    public detach() {
        if (this._handle) {
            this._handle.detach();
        }
        this._getStats.destroy();
    }
}
