import WildEmitter from 'wildemitter';
import { User, MediaSubscribeConfig, MediaSubscribePauseStatus } from './user';
import { MediaStats } from './get-stats';

export class UserControl extends WildEmitter {
    private _users: { [id: string]: User } = {}; // 
    private _usersInName: { [name: string]: User } = {};
    private _isSetAllMuted = false; // 

    private _subscribers: { [id: string]: User } = {}; // 
    private _subscribersInName: { [name: string]: User } = {};

    // 
    public registerUser(user: User) {
        const id = user.getId();
        const name = user.getName();
        this._users[id] = user;
        this._usersInName[name] = user;
        this._attachUserEvents(user);
        if (this._isSetAllMuted) {
            this.setMute(id, true);
        }
    }

    // 
    public registerSubscriber(user: User) {
        const id = user.getId();
        const name = user.getName();
        this._subscribers[id] = user;
        this._subscribersInName[name] = user;
        this._attachSubscriberEvents(user);
    }

    public getUser(userId: string): User | undefined {
        return this._users[userId];
    }

    public getSubscriber(userId: string): User | undefined {
        return this._subscribers[userId];
    }

    public getUserByName(userName: string): User | undefined {
        return this._usersInName[userName];
    }

    public getSubscriberByName(userName: string): User | undefined {
        return this._subscribersInName[userName];
    }

    public requestUserStream(userName: string): Promise<MediaStream> {
        if (this._usersInName[userName]) {
            return this._usersInName[userName].requestStream();
        }
        return Promise.reject('The user dose not exists.');
    }

    public requestSubscriberStream(userName: string): Promise<MediaStream> {
        if (this._subscribersInName[userName]) {
            return this._subscribersInName[userName].requestStream();
        }
        return Promise.reject('The subscriber dose not exists');
    }

    public pauseSubscribeUserStream(userName?: string | number, options?: MediaSubscribeConfig): Promise<void> {
        if (userName === undefined || userName === null) {
            const userNames = Object.keys(this._usersInName);
            userNames.forEach((name) => {
                this._usersInName[name].pauseSubscribe().then();
            });
            return Promise.resolve();
        }

        if (this._usersInName[userName]) {
            return this._usersInName[userName].pauseSubscribe(options);
        }
        return Promise.reject('The user dose not exists.');
    }

    public resumeSubscribeUserStream(userName?: string | number, options?: MediaSubscribeConfig): Promise<void> {
        if (userName === undefined || userName === null) {
            const userNames = Object.keys(this._usersInName);
            userNames.forEach((name) => {
                this._usersInName[name].resumeSubscribe().then();
            });
            return Promise.resolve();
        }

        if (this._usersInName[userName]) {
            return this._usersInName[userName].resumeSubscribe(options);
        }
        return Promise.reject('The user dose not exists.');
    }

    public getPauseSubscribeStatus(userName: string): MediaSubscribePauseStatus {
        return this._usersInName[userName]
            ? this._usersInName[userName].getPauseSubscribeStatus()
            : { video: false, audio: true };
    }

    public getMute(userName: string): boolean {
        return this._usersInName[userName] ? this._usersInName[userName].getMute() : false;
    }

    public setMute(userName: string, isMuted: boolean) {
        if (this._usersInName[userName]) {
            this._usersInName[userName].setMute(isMuted);
        }
        if (!isMuted) {
            this._isSetAllMuted = false;
        }
    }

    public setAllMute(isMuted: boolean) {
        const userIds = Object.keys(this._users);
        for (let id of userIds) {
            this.setMute(id, isMuted);
        }
        this._isSetAllMuted = true;
    }

    public isAllMute(): boolean {
        return this._isSetAllMuted;
    }

    public getUserMediaStats(userName: string): MediaStats | null {
        if (this._usersInName[userName]) {
            return this._usersInName[userName].getMediaStats();
        }
        return null;
    }

    private _attachSubscriberEvents(user: User) {
        const name = user.getName();
        user.on('update-stream', name, (stream: MediaStream) => {
            this.emit('subscriber:update-stream:' + name, name, 0, stream);
        });
        user.on('remove-stream', name, () => {
            this.emit('subscriber:remove-stream:' + name, name, 0);
        });
        user.on('signaling-status:*', name, (eventName: string, state: string) => {
            this.emit('subscriber:signaling-status:' + name + ':' + state, name, 0, state);
        });
        user.on('ice-status:*', name, (eventName: string, state: string) => {
            this.emit('subscriber:ice-status:' + name + ':' + state, name, 0, state);
        });
        user.on('destroy', () => {
            user.releaseGroup(name);
            delete this._subscribersInName[name];
            delete this._subscribers[user.getId()];
        });
        user.on('media-stats-update', name, (r: MediaStats) => {
            this.emit('subscriber:media-stats-update:' + name, name, 0, r);
        });
        user.on('pause-subscribe', name, (isAudioPaused: boolean, isVideoPaused: boolean) => {
            this.emit('subscriber:pause-subscribe:' + name, name, isAudioPaused, isVideoPaused);
        });
        user.on('resume-subscribe', name, (isAudioResumed: boolean, isVideoResumed: boolean) => {
            this.emit('subscriber:resume-subscribe:' + name, name, isAudioResumed, isVideoResumed);
        });
    }

    private _attachUserEvents(user: User) {
        const name = user.getName();
        user.on('update-stream', name, (stream: MediaStream) => {
            this.emit('update-stream:' + name, name, 0, stream);
        });
        user.on('remove-stream', name, () => {
            this.emit('remove-stream:' + name, name, 0);
        });
        user.on('signaling-status:*', name, (eventName: string, state: string) => {
            this.emit('signaling-status:' + name + ':' + state, name, 0, state);
        });
        user.on('ice-status:*', name, (eventName: string, state: string) => {
            this.emit('ice-status:' + name + ':' + state, name, 0, state);
        });
        user.on('destroy', () => {
            user.releaseGroup(name);
            delete this._usersInName[name];
            delete this._users[user.getId()];
        });
        user.on('media-stats-update', name, (r: MediaStats) => {
            this.emit('media-stats-update:' + name, name, 0, r);
        });
        user.on('pause-subscribe', name, (isAudioPaused: boolean, isVideoPaused: boolean) => {
            this.emit('pause-subscribe:' + name, name, isAudioPaused, isVideoPaused);
        });
        user.on('resume-subscribe', name, (isAudioResumed: boolean, isVideoResumed: boolean) => {
            this.emit('resume-subscribe:' + name, name, isAudioResumed, isVideoResumed);
        });
    }
}
